import React, { Component } from 'react'
import axios from "axios";
// import { userProfileData } from '../../services/userProfile';


// userProfileData
export default class Addevent extends Component {
  state = {
    title: '',
    date:'',
    startTime: '',
    endTime: '',
    maxCapacity:'',
    // attendees: '',
    // owner: '',
    description: '',
    image:'',
    googleLink:'',
    message: ''
  }

  handleChange = (event) => {
    const {name, value} = event.target;
    
    console.log(value);
    this.setState ({
      [name]: value
    });
  };


  
  handleSubmit = (event) => {
    event.preventDefault ();
      
    const { title, date, startTime,endTime, description, image, googleLink} = this.state;
    this.props.history.push("/events");

    console.log(this.state)
  
    // userProfileData(event).then (data => {
    //   if (data.message) {
    //     this.setState({
    //       message: data.message,
    //     })
    //   }
    // });

    if (title.length <5 ) {
        this.setState ({ 
        message: 'Please enter a longer title'
      })
    }
  
    axios.post("/api/events", {    
      title:title,
      date:date,
      startTime: startTime,
      endTime:endTime,
      owner: this.props.user._id,
      description: description,
      image:image,
      googleLink: googleLink,}).then( (event) => {
        console.log(event.data , "fetch event")
        this.setState({
          message:`Event created!`
        })
      }).catch( err => {
        console.log(err);
     })

    // this.props.history.push(`/events/${this.props.events._id}`);
      
    
  
  }//handleSubmit end

  // userProfileData (something).then (data => {
  //   if (data.message) {
  //     this.setState({
  //       message: data.message,
  //     });
  
 
  



  render() {
    console.log(this.props)
    console.log(`message`, this.state.message)
    return (
       <div>
            
            <p className="error">
              Message: {this.state.message}
            </p>

          <div> 
            <form onSubmit={this.handleSubmit}>
                <label htmlFor="title"> Event title: </label>
                <input 
                  type="text" 
                  name="title" 
                  id="title" 
                  
                  value={this.state.title} 
                  onChange={this.handleChange}/>

                <br/>

                <label htmlFor="date">Event date:</label>
                  <input 
                      type="date" 
                      name="date" 
                      id="date" 
                      value={this.state.date}
                      onChange={this.handleChange}
                        
                    />
                <br/>

                <label htmlFor="startTime "> Start time: </label>
                  <input 
                    type= "time" 
                    name="startTime" 
                    id="startTime" 
                    value={this.state.startTime}  
                    onChange={this.handleChange}

                  />
                <br/>
                <label htmlFor="endTime" > End time: </label>
                <input 
                type="time"
                name="endTime"
                id="endTime"
                value={this.state.endTime}
                onChange={this.handleChange}/>
                <br/>
                <label htmlFor="maxCapacity"> Maximum capacity: </label>
                <input 
                type="number"
                name="maxCapacity" 
                id="maxCapacity"
                value={this.state.maxCapacity}
                onChange={this.handleChange}/>
                <br/>
                {/* <label htmlFor="owner">Hosted by:  </label>
                <input 
                type="text" 
                name="owner" 
                id="owner"
                value={this.state.owner}
                onChange={this.handleChange}/>
                <br/> */}
                <label htmlFor="description">Description: </label>
                <input 
                type="text"
                name="description"
                id="description"
                value={this.state.description}
                onChange={this.handleChange}
                 />
                <br/>
                <label htmlFor="googleLink">Add Google link: </label>
                <input 
                type="link"
                name="googleLink"
                id="googleLink"
                value={this.state.googleLink}
                onChange={this.handleChange} />
                <br/>
                <label hmtlFor="image" >Upload an image: </label>
                <input type= "file"
                 name="image"
                  id="image"
                   accept="image/*">
                    </input>
                <button type='submit'> Add your event! </button>
              
            </form>
          </div>
      </div>
    );
  }
}
    
  





